<template>
<v-container text-xs-center>
  <v-layout row wrap>
    <v-flex md7 sm12 class="left">
      <h1>10 Lessones Options</h1>
      <div class="fee-box">
        <div class="fee-card" v-for="item in classes" :key="item.plan" v-if="!item.isFree">
          <h2>{{ item.period }}</h2>
          <h3>{{ item.time }}</h3>
          <h1>{{ item.price }}</h1>
          <v-btn flat>{{item.btn}}</v-btn>
        </div>
      </div>      
    </v-flex>
    <v-flex md5 sm12 class="right">2</v-flex>
  </v-layout>
</v-container>
</template>

<script>

export default {
  data () {
    return {
      classes: [
        {
          plan: 1,
          isFree: false,
          period: '1H / week',
          time: '10 lessons',
          price: '$599',
          btn: 'Enroll Now'
        },
        {
          plan: 2,
          isFree: false,
          period: '1.5H / week',
          time: '10 lessons',
          price: '$898',
          btn: 'Enroll Now'
        },
        {
          plan: 3,
          isFree: false,
          period: '2H / week',
          time: '10 lessons',
          price: '$1198',
          btn: 'Enroll Now'
        },
        {
          plan: 4,
          isFree: true,
          period: '1 lesson',
          time: '',
          pirce: '$65',
          btn: 'Enroll Now'
        },
        {
          plan: 5,
          isFree: true,
          period: '1 trail lesson',
          time: '',
          pirce: '$55',
          btn: 'Enroll Now'
        }
      ]
    }
  }
}
</script>

<style lang="sass" scoped>
@import ../assets/sass/juice
.left
  background: pink
.right
  background: orange
.fee-box
  display: flex
  justify-content: space-between

  .fee-card
    display: flex
    flex-direction: column
    width: 32%
    background: #111
    color: white
</style>


